.NavigationTabLabel
{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: large;
    color: var(--bodyTextColour);

    transition: 0.2s ease;
}

.NavigationTabLabel:hover
{
    font-weight: 600;
    color: var(--bodyTextColourLight);
}